*{
    margin: 0;
    padding: 0;
}
.banner-area{
    background-image: url('https://images.unsplash.com/photo-1494500764479-0c8f2919a3d8?crop=entropy&cs=srgb&fm=jpg&ixid=MnwxNDU4OXwwfDF8cmFuZG9tfHx8fHx8fHx8MTYzNzU5MjE4OA&ixlib=rb-1.2.1&q=85');
    background-position: center center;
    background-size:cover ;
    /* -webkit-background-size:cover ; */
    height: 100vh;
    width: auto;
    margin: auto;
}

.banner-text{
    position: absolute;
    width: 600px;
    height: 300px;
    margin: 15% 30%;
    text-align: center;
}
.banner-text h1{
    text-align: center;
    color:rgb(233, 185, 255);
    text-transform: uppercase;
    font-size: 60px;
    font-family: 'poppins' , sans-serif;
}
.banner-text p{
    color: #fff;
    font-size: 18px;
}
.banner-text a{
    border: 1px solid #fff;
    padding: 10px 25px;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 14px;
    margin-top: 20px;
    display: inline-block;
    color: #fff;
}
.banner-text a:hover{
    background-color: white;
    color: rgba(68, 25, 109, 0.623);
    font-weight: bold;
}
.card-container{
  -webkit-perspective: 900px;
          perspective: 900px;
  margin-right: 20px;
  margin-bottom: 60px;
}

.cards{
  position: relative;
  width: 100px;
  height: 100px;
  transition: all 0.6s ease;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.front,.back{
  position: absolute;
  background: #7FC6A4;
  top: 0;
  left: 0;
  width: 100px;
  height: 100px;
  border-radius: 5px;
  color: white;
  box-shadow: 0 27px 55px 0 rgba(0, 0, 0, 0.3), 0 17px 17px 0 rgba(0, 0, 0, 0.15);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.front{
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
}

.back{
  display: flex;  
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.card-container:hover .cards{
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.back{
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.name{
    font-size: 12px;
    color: black;
    font-weight: bold;
    justify-content: center;
    cursor: pointer;
}

.category-btn{
  width: 100%;
  height: 20%;
  font-size: 10px;
}

button {
    margin-top: 1rem;
      display: block;
      border: none;
      background-color: #00a8ff;
      color: black;
      font-weight: bold;
      border-radius: 6px;
      outline: none;
      cursor: pointer;
      transition: .3s linear;
    }
    
    button:hover {
      -webkit-transform: rotateX(15deg);
              transform: rotateX(15deg);
      box-shadow: 0 15px 15px #00a8ff;
    }

    .notes{ 
      color: black;
    }

    .fw-bold{ 
      font-size: 24px;
      font-weight: bold;
      color: white;
    }

    .fz-bold{ 
      font-size: 24px;
      font-weight: bold;
      color: #00a8ff;
    }

    .fw1-bold{ 
      font-size: 24px;
      font-weight: bold;
      color: gray;
    }



* { box-sizing: border-box; }

body {
	font-family: "HelveticaNeue-Light","Helvetica Neue Light","Helvetica Neue",Helvetica,Arial,"Lucida Grande",sans-serif;
  color:white;
  font-size:12px;
  background:#333
  url(/static/media/desola-lanre-ologun-zYgV-NGZtlA-unsplash.149dd561.jpg);
  background-size: cover;
}

form {
 	background:#111; 
  width:300px;
  margin:30px auto;
  border-radius:0.4em;
  border:1px solid #191919;
  overflow:hidden;
  position:relative;
  box-shadow: 0 5px 10px 5px rgba(0,0,0,0.2);
}

form:after {
  content:"";
  display:block;
  position:absolute;
  height:1px;
  width:100px;
  left:20%;
  background:linear-gradient(left, #111, #444, #b6b6b8, #444, #111);
  top:0;
}

form:before {
 	content:"";
  display:block;
  position:absolute;
  width:8px;
  height:5px;
  border-radius:50%;
  left:34%;
  top:-7px;
  box-shadow: 0 0 6px 4px #fff;
}

.inset {
 	padding:20px; 
  border-top:1px solid #19191a;
}

form h1 {
  font-size:18px;
  text-shadow:0 1px 0 black;
  text-align:center;
  padding:1px 0;
  border-bottom:1px solid rgba(0,0,0,1);
  position:relative;
}

form h1:after {
 	content:"";
  display:block;
  width:250px;
  height:100px;
  position:absolute;
  top:0;
  left:50px;
  pointer-events:none;
  -webkit-transform:rotate(70deg);
          transform:rotate(70deg);
  background:linear-gradient(50deg, rgba(255,255,255,0.15), rgba(0,0,0,0));
  
}

label {
 	color:#666;
  display:block;
  padding-bottom:9px;
}

input[type=text],
input[type=password] {
    color: white;
 	width:100%;
  padding:8px 5px;
  background:linear-gradient(#1f2124, #27292c);
  border:1px solid #222;
  box-shadow:
    0 1px 0 rgba(255,255,255,0.1);
  border-radius:0.3em;
  margin-bottom:20px;
}

input[type=number],
input[type=date] {
  color: white;
  width:100%;
 padding:8px 5px;
 background:linear-gradient(#1f2124, #27292c);
 border:1px solid #222;
 box-shadow:
   0 1px 0 rgba(255,255,255,0.1);
 border-radius:0.3em;
 margin-bottom:20px;
}

input[as=textarea]{
  color: white;
  width:100%;
 padding:8px 5px;
 background:linear-gradient(#1f2124, #27292c);
 border:1px solid #222;
 box-shadow:
   0 1px 0 rgba(255,255,255,0.1);
 border-radius:0.3em;
 margin-bottom:20px;
}



label[for=remember]{
 	color:white;
  display:inline-block;
  padding-bottom:0;
  padding-top:5px;
}

input[type=checkbox] {
 	display:inline-block;
  vertical-align:top;
}

.p-container {
 	padding:0 20px 20px 20px; 
}

.p-container:after {
 	clear:both;
  display:table;
  content:"";
}

.p-container span {
  display:block;
  float:left;
  color:#0d93ff;
  padding-top:8px;
}

button[type=submit] {
 	padding:5px 20px;
  border:1px solid rgba(0,0,0,0.4);
  text-shadow:0 -1px 0 rgba(0,0,0,0.4);
  box-shadow:
    inset 0 1px 0 rgba(255,255,255,0.3),
    inset 0 10px 10px rgba(255,255,255,0.1);
  border-radius:0.3em;
  background:#0184ff;
  color:white;
  float:right;
  font-weight:bold;
  cursor:pointer;
  font-size:13px;
  margin-bottom: inherit;
}

button[type=submit]:hover {
  box-shadow:
    inset 0 1px 0 rgba(255,255,255,0.3),
    inset 0 -10px 10px rgba(255,255,255,0.1);
}

input[type=text]:hover,
input[type=password]:hover,
label:hover ~ input[type=text],
label:hover ~ input[type=password] {
 	background:#27292c;
}
